/* mixin */
/* mixin end */
/*reset*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
  height: 100%;
}
body {
  min-width: 320px;
  height: 100%;
  font-size: 1.6rem;
  color: #2c2c2c;
  position: relative;
}
h1,
h2,
h3,
h4 {
  font-weight: 400;
  font-size: 1.6rem;
}
input,
select {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  outline: 0;
  -webkit-appearance: none;
}
input::-webkit-input-placeholder {
  color: #606060;
}
li {
  list-style: none;
}
a {
  color: #2c2c2c;
  text-decoration: none;
}
i {
  font-style: inherit;
}
input::-webkit-input-placeholder {
  color: #e8e8e8;
}
input:-moz-placeholder {
  color: #e8e8e8;
}
/* reset end */
/* login */
#login-buttons {
  line-height: 5rem;
  position: absolute;
  top: 0;
  left: 0;
  padding-left: 1rem;
  z-index: 3;
}
#login-buttons a {
  text-decoration: none;
}
#login-buttons .login-link-text {
  color: #fff;
}
.accounts-dialog {
  color: #41b883 !important;
  font-size: 2.2rem;
  padding: 2rem!important;
  position: fixed!important;
  width: 100%;
  height: 100%;
  top: 0!important;
  left: 0!important;
  border: 0!important;
  border-radius: 0!important;
  margin: 0!important;
  background-color: #fff;
}
.accounts-dialog input,
.accounts-dialog label {
  text-indent: 1rem;
  height: 4rem!important;
  margin: .5rem 0 1.5rem;
}
.login-button {
  height: 4rem;
  line-height: 4rem!important;
  font-size: 1.6rem!important;
  margin-bottom: 1rem!important;
  padding: 0!important;
  background-color: #41b883 !important;
  border: 0!important;
}
.login-close-text {
  float: right!important;
  height: 1rem!important;
  font-size: 0!important;
  margin-bottom: 3rem;
  text-decoration: none!important;
}
.login-close-text::before {
  display: block;
  font-size: 2rem;
  content: 'x';
  width: 3rem;
  height: 3rem;
  line-height: 3rem;
  position: absolute;
  top: -1rem;
  right: -1rem;
  border: 1px solid #41b883;
  border-radius: 50%;
  text-align: center;
  color: #41b883;
}
/* login end */
#app,
.page-wrap,
.page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.page {
  background-color: #f3f3f3;
}
.page-title {
  font-size: 2rem;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
  background-color: #41b883;
  color: #ffffff;
  overflow: hidden;
  position: relative;
}
.page-title_back {
  position: absolute;
  left: 20px;
  font-size: 2rem;
  color: #fff;
}
.page-title_msg-count {
  position: absolute;
  top: 0;
  right: 1rem;
  font-size: 1.6rem;
  font-weight: normal;
  color: #f3f3f3;
}
.page-title_msg-count--unread {
  color: #fff;
  font-weight: bold;
}
.msg-list {
  overflow: auto;
  position: absolute;
  top: 5rem;
  left: 0;
  right: 0;
  bottom: 0;
}
.msg-item--unread:before {
  background: #FB8181;
}
.msg-item {
  overflow: hidden;
  height: 6rem;
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
  position: relative;
}
.msg-item:before {
  content: '';
  position: absolute;
  width: .5rem;
  height: .5rem;
  border-radius: 50%;
  left: 0.6rem;
  top: 2.8rem;
  z-index: 2;
}
.msg-item_name {
  position: absolute;
  left: 1.5rem;
  top: 1rem;
  text-align: center;
  line-height: 4rem;
  width: 4rem;
  height: 4rem;
  font-size: 1.2rem;
  border-radius: 50%;
  background-color: #41b883;
  color: #ffffff;
}
.msg-item_time {
  position: absolute;
  top: .5rem;
  right: .5rem;
  font-size: 1.3rem;
}
.msg-item_content {
  width: 100%;
  padding: 2rem 2rem 2rem 6.5rem;
  height: 2rem;
  overflow: hidden;
  line-height: 2rem;
}
/* chat */
.chat-box_list {
  overflow: auto;
  position: absolute;
  top: 5rem;
  left: 0;
  right: 0;
  bottom: 5rem;
  background-color: #fff;
}
.chat-box_msg {
  position: absolute;
  bottom: 0;
  left: 0 ;
  height: 5rem;
  width: 100%;
  padding: 10px;
  background: #ececec;
}
.chat-box_input {
  display: block;
  height: 100%;
  width: 100%;
  font-size: 1.5rem;
  border: none;
  text-indent: 20px;
}
.chat-item {
  padding: 20px;
  border-bottom: 1px dashed #e8e8e8;
}
.chat-item_info {
  margin-bottom: 10px;
}
.chat-item_info .name {
  float: left;
  font-size: 1.3rem;
}
.chat-item_info .time {
  float: right;
  font-size: 1.2rem;
}
.chat-item_info:after {
  content: '';
  display: block;
  clear: both;
}
